.footer{
  width: 100%;
  float: left;
  text-align: center;
  padding: 38px 0;
  box-sizing: border-box;
  p{
    display: inline-block;
    text-align: center;

    color: #959595;
    font-family: $fnb;
    font-size: 20px;
    font-weight: 300;
    line-height: 28px;
  }
}
.modal_form {
	width: 957px;
	text-align: center;
	border-radius: 5px;
	border: 1px #eaeaea solid;
	background: #fff;
	position: fixed; /* o oo o  o o  o  */
	top: 45%; /* oa  45%, oa 5% o  */
	left: 32%; /* ooa aa a */
	margin-top: -150px;
	margin-left: -150px; /*   a o css, oa o    oo   o ooo =) */
	display: none; /*  oo oo oa  oo  */
	opacity: 0; /* oo oao  aoa */
	z-index: 5; /* oo oo  ao o o */
	padding: 20px 10px;
  .wrap-form{
    width: 92%;
    margin-left: -40px;
  }
  h2{
    @include h2-title;
  }
  h3{
    display: inline-block;
    color: #0d0d0d;
    font-family: $fn;
    font-size: 26px;
    font-weight: 400;
    line-height: 40px;
    float: left;
    width: 100%;
    padding: 0 20%;
  }
  .thumb{
    display: inline-block;
    float: left;
    position: relative;
    width: 100%;
    overflow: hidden;
    padding: 30px 0;
    box-sizing: border-box;
    img{
      object-fit: contain;
      margin: 0 auto;
      float: none;
    }
  }
  .send_msg{
    display: inline-block;
    float: none;
    cursor: pointer;
    outline: none;
    color: #fff;
    font-family: $fn;
    font-size: 17px;
    font-weight: 300;
    line-height: 32px;
    // min-width: 231px;
    background-color: #000;
    border: none;
    padding: 10px 40px;
    box-sizing: border-box;
    // border: 1px solid #979797;
    text-align: center;
    margin-top: 15px;
    margin-left: -8px;
    &:hover{
      color: #fff;
      background-color: rgba(29, 27, 27, 0.87);
    }
  }
  span{
    // @include span-title;
    display: inline-block;
    float: none;
    margin: 0 auto;
    width: 100%;
    font-family: $fnb;
    color: #959595;
    font-size: 20px;
  }
  .form_row{
    width: 100%;
    float: left;
    display: inline-block;
    margin: 17px 0;
    span{
      float: left;
      padding: 0;
      display: inline-block;
      width: initial;
      color: #959595;
      font-family: $pdr;
      font-size: 20px;
      font-weight: 400;
      line-height: 17px;
    }
    input, select{
      float: left;
      border-top: none;
      border-right: none;
      border-left: none;
      padding-left: 15px;
      width: 180px;
      margin: 0 10px;
      color: #000;
      font-family: $fnb;
      font-size: 23px;
      font-weight: 300;
      line-height: 20px;
      border-bottom: 1px solid #979797;
      position: relative;
      top: -11px;
      outline: none;

      &:focus{
        background-color: #fff;
        border-bottom: 1px solid #000;
      }

    }
    .inputError{
      border-bottom: 1px solid #ff3333;
    }
    .errorMsgClass{
      color: #ff3333;
      text-transform: uppercase;
      font-family: $fnm;
      font-size: 14px;
      letter-spacing: 1.71px;
      position: absolute;
      left: 25%;
      margin-top: 23px;
      width: initial;
      padding: initial;
    }
    select{
      padding-left: 0;
      top: -9px;
      background-image: url("../img/rect.png");
      background-position: 97% 1%;
      background-repeat: no-repeat;
      background-size: 6%;
      line-height: 28px;
      option{
        font-size: 15px;
        font-family: $pdr;
        color: #000;
        // font-weight: 300;
        margin: 2px 0;

      }
    }

  }
}
/* oa a   o  a) */
.modal_form .modal_close {
	width: 21px;
	height: 21px;
	position: absolute;
  top: 27px;
  right: -36px;
	cursor: pointer;
	display: block;
  font-size: 50px;
  color: #fff;
  font-family: $fnm;
  &:after{
    content: "";
    display: inline-block;
    width: 65px;
    position: absolute;
    height: 1px;
    background-color: #fff;
    -webkit-transform: rotate(-45deg);
   -moz-transform: rotate(-45deg);
   -o-transform: rotate(-45deg);
  }
  &:before{
    content: "";
    display: inline-block;
    width: 65px;
    position: absolute;
    height: 1px;
    background-color: #fff;
    -webkit-transform: rotate(45deg);
   -moz-transform: rotate(45deg);
   -o-transform: rotate(45deg);

  }
}
#modal2{
  padding-bottom: 70px;
}
/* ooa */
#overlay {
	z-index:3; /* ooa oa   o o aa, o  o oaoo oa */
	position:fixed; /* a a  a */
	background-color:#000; /* a */
	opacity:0.8; /* o oo oaa */
	-moz-opacity:0.8; /*      */
	filter:alpha(opacity=80);
	width:100%;
	height:100%; /* ao o  a */
	top:0; /*   a 0, oa oa! */
	left:0;
	cursor:pointer;
	display:none; /*  oo oo  ) */
}
