.clients{
  width: 100%;
  float: left;
  position: relative;
  text-align: center;
  h2{
    @include h2-title;
    margin-top: 68px;
  }
  span{
    @include span-title;
    margin-bottom: 49px;
    padding: 0 34%;
  }
  .more-clients{
    width: 100%;
    float: left;
    display: inline-block;
    text-align: center;
    color: #959595;
    font-family: $fnb;
    font-size: 20px;
    font-weight: 300;
    line-height: 28px;
    margin-top: 34px;
    margin-bottom: 0;
  }
}
.clients__item{
  display: inline-block;
  float: left;
  width: 352px;
  height: 200px;
  background-color: rgba(160, 160, 160, 0.1);
  margin:10px 10px 0 0;
  overflow: hidden;
  text-align: center;
  position: relative;
  &:nth-of-type(4), &:nth-of-type(8){
    margin-right: 0;
  }
  img{
    float: none;
    display: inline-block;
    margin: 0 auto;
    object-fit: contain;
    position: relative;
    position: relative;
		top: 50%;
		-webkit-transform: translate(0,-50%);
		   -moz-transform: translate(0,-50%);
		    -ms-transform: translate(0,-50%);
		     -o-transform: translate(0,-50%);
		        transform: translate(0,-50%);
  }
  h3{
    width: 100%;
    text-align: center;
    color: #000;
    font-family: $fn;
    font-size: 32px;
    font-weight: 500;
    margin-top: 44px;
  }
  span{
    width: 100%;
    text-align: center;
    padding: 0 50px;
    box-sizing: border-box;
    color: #959595;
    font-family: $fn;
    font-size: 20px;
    font-weight: 300;
    line-height: 28px;
  }
}
