.calendar{
  width: 100%;
  float: left;
  position: relative;
  text-align: center;
  padding-top: 91px;
  box-sizing: border-box;
  h2{
    @include h2-title;
    margin-top: 47px;
  }
  span{
    @include span-title;
    padding: 0 35%;
  }
  .owl-carousel{
    width: 100%;
    display: inline-block;
    margin-top: 28px;
    .item{
      width: 100%;
      h4{
        width: 100%;
        text-align: center;
        color: #000000;
        font-family: $fn;
        font-size: 17px;
        font-weight: 300;
        line-height: 32px;
        text-transform: uppercase;
      }
    }
    .owl-controls{
      top: 7px;
      position: absolute;
      text-align: center;
      width: 100%;

      .owl-nav{
        margin: 0 auto;
        float: none;
        width: 12%;
        display: -webkit-inline-box;
        position: relative;
         .owl-next{
          size: 10px 16px;
          position: absolute;
          right: 0;
          &:after{
            left: 0;
            content: "";
            display: inline-block;
            width: 12px;
            position: absolute;
            height: 1px;
            bottom: 7px;
            background-color: #000;
            -webkit-transform: rotate(-400deg);
           -moz-transform: rotate(-400deg);
           -o-transform: rotate(-400deg);
          }
          &:before{
            left: 0;
            content: "";
            display: inline-block;
            width: 12px;
            position: absolute;
            height: 1px;
            background-color: #000;
            -webkit-transform: rotate(400deg);
           -moz-transform: rotate(400deg);
           -o-transform: rotate(400deg);

          }
          &:hover{
            &:after{
              background-color: #ff2c01;
            }
            &:before{
              background-color: #ff2c01;
            }
          }
        }
        .owl-prev{
          size: 10px 16px;
          position: absolute;
          &:after{
            left: 0;
            content: "";
            display: inline-block;
            width: 12px;
            position: absolute;
            height: 1px;
            bottom: 7px;
            background-color: #000;
            -webkit-transform: rotate(-140deg);
           -moz-transform: rotate(-140deg);
           -o-transform: rotate(-140deg);
          }
          &:before{
            left: 0;
            content: "";
            display: inline-block;
            width: 12px;
            position: absolute;
            height: 1px;
            background-color: #000;
            -webkit-transform: rotate(140deg);
           -moz-transform: rotate(140deg);
           -o-transform: rotate(140deg);
          }
          &:hover{
            &:after{
              background-color: #ff2c01;
            }
            &:before{
              background-color: #ff2c01;
            }
          }
        }
      }
    }
  }
  table{
    margin: 6px auto 0;
    width: 643px;
    display: inline-block;
    border-spacing: 11px 8px;
    border-collapse: separate;
    tr{
      // width: 100%;
      float: left;
      position: relative;
      padding: 3px 0;
      box-sizing: border-box;
      // border-bottom: 1px solid #f5f5f5;

      td{
        size: 78px;
        position: relative;
        background-color: #fff;
        border-spacing: 6px;
        box-sizing: border-box;
        font-family: $fnb;
        font-size: 23px;
        font-weight: 300;
        color: #ececec;
        cursor: pointer;
        // transition: 0.2s 1s;
        // visibility: hidden;
        // &:first-of-type{
        //   width: 169px;
        //   font-family: $fnb;
        //   font-size: 14px;
        //   font-weight: 300;
        //   color: #ececec;
        //   background-color: #fff;
        //   // cursor: none;
        //   &:hover{
        //     color: #000;
        //     background-color: #fff;
        //     transition: 0.3s;
        //   }
        // }
        &:hover{
          background-color: #000;
          color: #fff;
          duration: 0.5s;

          .day-popup, .day-popup-free_day{
            display: block;
            visibility: visible;
            transition-delay: 1s;
          }
        }
      }
      .first-date{
        font-size: 14px;
        padding: 0;
        color: #959595;
        font-family: $fnb;
        position: absolute;
        left: 0;
        margin-top: 24px;
      }
      .day-popup, .day-popup-free_day{
        display: none;
        height: 95px;
        position: absolute;
        color: #959595;
        font-family: $fnb;
        font-size: 14px;
        font-weight: 300;
        line-height: 20px;
        background-color: #f5f5f5;
        text-align: center;
        padding: 15px 0;
        box-sizing: border-box;
        z-index: 3;
        left: -19px;
        top: -105px;
        width: 118px;
        &::after{
          content: "";
          width: 0;
        	height: 0;
          border-top: 10px solid #f5f5f5;
          border-right: 10px solid transparent;
          border-left: 10px solid transparent;
          content: "";
          bottom: -9px;
          position: absolute;
          left: 41%;
        }
      }
      .day-popup-free_day{
        padding: 35px 0;
      }
      .busy_day{
        background-color: #ffe2e2;
        color: #000;
      }
      .reserved{
        background-color: #fff6e1;
        color: #000;
        cursor: default;
      }
      .free_day{
        border: 1px solid #eee;
        background-color: #fff;
        color: #000;
      }
    }
    tbody{
      // overflow-y: scroll;
      // height: 495px;
      // width: 100%;
      display: block;
      // padding-right: 30px;
      box-sizing: border-box;
      tr{
        border-bottom: 1px solid #f5f5f5;
        // &:after{
        //   content: "";
        //   position: absolute;
        //   background-color: #f5f5f5;
        //   height: 1px;
        //   right: 1%;
        //   width: 91%;
        //   margin-top: 3px;
        // }
      }
      tr:last-of-type{
        border-bottom: none;
      }
      box-sizing: border-box;

      td.active{
        color: #000;


        &:hover{
          color: #fff;
          transition: 0.3s;
        }
      }
    }
    ::-webkit-scrollbar-button {
    background-image:url('');
    background-repeat:no-repeat;
    width:15px;
    height:0px
    }

    ::-webkit-scrollbar-track {
    background-color:#ecedee
    }

    ::-webkit-scrollbar-thumb {
    -webkit-border-radius: 0px;
    border-radius: 0px;
    background-color:#000;
    }

    ::-webkit-scrollbar-thumb:hover{
    background-color:#212626;
    }

    ::-webkit-resizer{
    background-image:url('');
    background-repeat:no-repeat;
    width:4px;
    height:0px
    }

    ::-webkit-scrollbar{
    width: 15px;
    }
    thead{
      width: 100%;
      display: inline-block;
      border-top: 1px solid #bcbcbc;
      border-bottom: 1px solid #bcbcbc;
      // &::before{
      //   content: "";
      //   width: 770px;
      //   border-top: 1px solid #bcbcbc;
      //   content: "";
      //   position: absolute;
      //   right: 21.5%;
      //   margin-top: -13px;
      // }
      tr{
        width: 100%;
        padding: 11px 0;
        display: inline-block;
        th{
          text-transform: uppercase;
          // width: 78px;
          width: 14%;
          float: left;
          font-family: $fn;
          font-size: 14px;
          font-weight: 300;
          color: #959595;
          background-color: #fff;
          text-align: center;
        }
      }
    }
  }
}

.calendar-row{
  float: none;
  margin: 26px auto 20px;
  width: 44%;
  display: inline-block;
  p{
    float: left;
    display: inline-block;
    position: relative;
    text-align: left;
    color: #959595;
    font-family: $fn;
    font-size: 14px;
    font-weight: 300;
    line-height: 14px;
    margin: 0 10px;
    span{
      size: 14px;
      float: left;
      position: relative;
      display: block;
      padding: 0;
      margin-right: 5px;
    }
    .pink{
      background-color: #ffe1e1;
    }
    .yellow{
      background-color: #fff6e1;
    }
    .white{
      background-color: #fff;
      border: 1px solid #ccc;
    }
  }
}
