.business{
  width: 100%;
  float: left;
  position: relative;
  text-align: center;
  h2{
    @include h2-title;
    margin-bottom: 29px;
  }
  span{
    @include span-title;
    padding: 0 39%;
  }

}
.wrap-form{
  width: 75%;
  float: none;
  margin: 0 auto;
}
form{
  display: inline-block;
  float: none;
  margin: 30px auto 90px;
  min-width: 723px;
  margin-left: 180px;
  // max-width: 1200px;
  .send_msg{
    display: inline-block;
    cursor: pointer;
    outline: none;
    color: #fff;
    font-family: $fn;
    font-size: 17px;
    font-weight: 300;
    line-height: 32px;
    min-width: 231px;
    background-color: #000;
    border: none;
    padding: 10px 0;
    box-sizing: border-box;
    // border: 1px solid #979797;
    text-align: center;
    margin-top: 15px;
    margin-left: 241px;
      float: left;
    &:hover{
      color: #fff;
      background-color: #000;
      transition: 0.3s;
    }
  }
}
.form_row{
  width: 100%;
  float: left;
  display: inline-block;
  margin: 17px 0;
  position: relative;
  label{
    float: left;
    padding: 0;
    display: inline-block;
    width: initial;
    color: #959595;
    font-family: $pdr;
    font-size: 20px;
    font-weight: 400;
    line-height: 17px;
  }
  input, select{
    -webkit-appearance: none;
    float: left;
    border-top: none;
    border-right: none;
    border-left: none;
    padding-left: 15px;
    width: 180px;
    min-width:180px!important;
    max-width:250px!important;
    margin: 0 10px;
    color: #000;
    font-family: $fnb;
    font-size: 23px;
    font-weight: 300;
    line-height: 20px;
    border-radius: 0;
    background-color: transparent;
    box-shadow: none;
    border-bottom: 1px solid #979797;
    position: relative;
    text-align: center;
    top: -11px;
    outline: none;

    &:focus{
      background-color: #fff;
      border-bottom: 1px solid #000;
    }
    &::-webkit-input-placeholder {color:#dadada; font-family: $fnl; font-size: 23px;}
    &::-moz-placeholder          {color:#dadada; font-family: $fnl; font-size: 23px;}
    &:-moz-placeholder           {color:#dadada; font-family: $fnl; font-size: 23px;}
    &:-ms-input-placeholder      {color:#dadada; font-family: $fnl; font-size: 23px;}

  }
  .inputError{
    border-bottom: 1px solid #ff3333!important;
  }
  .errorMsgClass{
    color: #ff3333;
    text-transform: uppercase;
    font-family: $fnm;
    font-size: 14px;
    letter-spacing: 1.71px;
    position: absolute;
    left: 25%;
    margin-top: 23px;
    width: initial;
    padding: initial;
  }
  select{
    padding-left: 0;
    top: -9px;
    background-image: url("../img/rect.png");
    background-position: 97% 57%;
    background-repeat: no-repeat;
    background-size: 6%;
    line-height: 28px;
    option{
      font-size: 15px;
      font-family: $pdr;
      color: #000;
      // font-weight: 300;
      margin: 2px 0;

    }
  }

}
