$fn: 'dem';
$fnb: 'FuturaNewBook';
$fnm: 'FuturaNewMedium';
$pdr: 'PlayfairDisplayRegular';
$fnl: 'FuturaNewLightReg';


@font-face {
font-family: 'dem';
src: url('../fonts/FuturaNewDemi/FuturaNewDemi.eot') format('embedded-opentype'),
url('../fonts/FuturaNewDemi/FuturaNewDemi.woff2') format('woff2'),
url('../fonts/FuturaNewDemi/FuturaNewDemi.woff') format('woff'),
url('../fonts/FuturaNewDemi/FuturaNewDemi.ttf') format('truetype'),
url('../fonts/FuturaNewDemi/FuturaNewDemi.svg') format('svg');
font-weight: normal;
}
