.faq{
  width: 100%;
  float: left;
  position: relative;
  text-align: center;
  padding-top: 18px;
  padding-bottom: 30px;
  box-sizing: border-box;
  h2{
    @include h2-title;
        margin-top: 78px;
  }
  span{
    @include span-title;
    padding: 0 33%;
    margin-top: 15px;
    margin-bottom: 36px;
    a{
      color: #959595;
      font-family: $fn;
      font-size: 20px;
      font-weight: 300;
      line-height: 28px;
      &:hover{
        border-bottom: 2px solid #f33;
        transition: 0.3s;
      }
    }
  }
}
.faq__row{
  width: 840px;
  float: none;
  display: inline-block;
  position: relative;
  margin: 0 auto;
  padding: 22px 0;
  box-sizing: border-box;
  border-bottom: 2px solid #f5f5f5;
  &:last-of-type{
    border-bottom: none;
  }
}
.faq__item{
  width: 100%;
  float: left;
  position: relative;
  display: inline-block;
  cursor: pointer;
  opacity: 0.3;
  margin: 0;
  .arrow{
    float: left;
    display: inline-block;
    background-image: url("../img/arrow.png");
    size: 17px 12px;
    background-repeat: no-repeat;
    background-size: contain;
    padding: 0;
    position: relative;
    // top: 9px;
    top: -5px;
    margin-right: 16px;
    margin-bottom: 0;
    -webkit-transition: all 0.7s ease;
    -moz-transition: all 0.7s ease;
    -o-transition: all 0.7s ease;
    transition: all 0.7s ease;
  }
  p{
    color: #000;
    font-family: $pdr;
    font-size: 20px;
    font-weight: 400;
    line-height: 35px;
    text-align: left;
    display: inline-block;
    width: 96%;
    margin: 0;
  }
}

.faq__item_list{
  width: 810px;
  float: right;
  display: none;
  position: relative;
  background-color: #f5f5f5;
  padding: 31px;
  box-sizing: border-box;
  margin-top: 15px;
&:before{
  content: "";
    position: absolute;
    display: block;
    width: 0;
  	height: 0;
  	border-left: 9px solid transparent;
  	border-right: 9px solid transparent;
  	border-bottom: 10px solid #f5f5f5;
    top: -8px;
    left: 7%;

  }
  .thumb{
    width: 67px;
    position: relative;
    float: left;
    display: inline-block;
    margin-right: 25px;
    .pic{
      size: 67px;
      overflow: hidden;
      border-radius: 50%;
    }
    .name{
      display: inline-block;
      text-align: left;
      color: #959595;
      font-family: $fnb;
      font-size: 14px;
      font-weight: 300;
      line-height: 20px;
      width: 100%;
      text-align: center;
      padding: 0;
      margin-top: 10px;
      margin-bottom: 0;
    }
  }
  p{
    display: inline-block;
    text-align: left;
    color: #0d0d0d;
    font-family: $pdr;
    font-size: 17px;
    font-weight: 400;
    line-height: 32px;
    width: calc(100% - 92px);
  }
}

.rotated {
  transform: rotate(180deg);
  -ms-transform: rotate(180deg); /* IE 9 */
  -moz-transform: rotate(180deg); /* Firefox */
  -webkit-transform: rotate(180deg); /* Safari and Chrome */
  -o-transform: rotate(180deg); /* Opera */
  -webkit-transition: all 0.7s ease;
    -moz-transition: all 0.7s ease;
    -o-transition: all 0.7s ease;
    transition: all 0.7s ease;
}
