.first-slider{
  width: 100%;
  float: left;
  position: relative;
  text-align: center;
  .title__tabs{
   width: 100%;
  //  float: left;
   position: relative;
   box-sizing: border-box;
   margin-bottom: 10px;
    overflow: hidden;
   margin:0 auto;

   ul{
     padding: 0;
     background-color: #fff;
     width: 100%;
     display: block;
     margin: 0;
     width: 2600px;

     li, .slick-slide{
       width: 352px!important;
       float: left;
       display: inline-block;
       list-style-type: none;
       text-align: center;
      position: relative;
      text-align: center;
      background-color: rgba(160, 160, 160, 0.1);
      outline: none;
      cursor: pointer;
      margin: 0 10px 0 0;
      height: 200px;
      padding: 51px 39px;
      box-sizing: border-box;
      outline: none;
      &:hover{
        color: #fff;
        transition: 0.3s;
        background-color: #000;
        h2, span{
          color: #fff;
        }
      }
       h2{
         color: #000;
         font-family: $fnm;
         font-size: 32px;
         font-weight: 500;
         width: 100%;
         text-align: center;
         margin-top: 0;
         margin-bottom: 10px;
         display: inline-block;
       }
       span{
         color: #959595;
         font-family: $fnb;
         font-size: 20px;
         font-weight: 300;
         line-height: 28px;
         width: 100%;
         text-align: center;
       }
      //  a{
      //    color: #1d1d1d;
      //    font-size: 12px;
      //    font-family: $arib;
      //    padding: 20px 0;
      //    box-sizing: border-box;
      //    text-transform: uppercase;
      //    text-decoration: none;
      //  }
     }
   }
 }
 .page__tabs_target{
   width: 100%;
   float: left;
   display: inline-block;
   background-color: #fff;
   text-align: center;
 }
 li.page__tabs_target.page__tabs_active{
   display: block;
   background-color: #000;
     h2, span{
       color: #fff;
     }
     &:hover{
      background: #000;
      transition: 0.3s;
     }
 }
 .box-connent{
   width: 100%;
   float: left;
   position: relative;
   box-sizing: border-box;
 }

 .page__tabcontent{
   width: 100%;
   float: left;
   box-sizing: border-box;
   .page__tabcontent_firstsliders{
     width: 100%;
     position: relative;
     .item{
       width: 100%;
       float: left;
       height: 818px;
       position: relative;
       outline: none;
       cursor: pointer;
       img{
         size: 100%;
       }
     }
   }
 }

 .one{
   display: block;
 }
 .two, .three, .four, .five, .six{
   display: none;
 }
}
