.about-designer{
  width: 100%;
  float: left;
  position: relative;

  margin-bottom: 10px;
}

.about-designer__about{
  width: 50%;
  float: left;
  position: relative;
  padding: 54px 30px 55px 119px;
  box-sizing: border-box;
  margin-top: 7px;
  background-color: rgba(161, 161, 161, 0.1);
  span{
    float: left;
    display: inline-block;
    font-family: $fnm;
    font-size: 14px;
    font-weight: 500;
    letter-spacing: 2px;
    line-height: 0;
    color: #000;
    position: relative;
    &:after{
      content: "";
      display: inline-block;
      height: 2px;
      background-color: #f33;
      width: 99%;
      position: absolute;
      margin-top: 13px;
      left: 0;
    }
  }
  h2{
    font-family: $fnm;
    font-size: 50px;
    font-weight: 500;
    line-height: 0;
    width: 100%;
    text-align: left;
    display: inline-block;
    float: left;
    margin-top: 57px;
    margin-bottom: 42px;
    // letter-spacing: -1px;

  }
  p{
    width: 100%;
    float: left;
    position: relative;
    display: inline-block;
    color: #0d0d0d;
    font-family: $pdr;
    font-size: 17px;
    line-height: 29.44px;
    margin-bottom: 36px;
    &:first-of-type{
      color: #959595;
      font-family: $pdr;
      font-size: 20px;
      font-weight: 400;
      line-height: 34.64px;
      margin-bottom: 22px;
      width: 84%;
    }
  }
  .tin_tak{
    @include tin_tak;
    float: left;
    padding: 8px 0 9px;
    box-sizing: border-box;
    &:first-of-type{
      margin-right: 12px;
      background-color: #000;
      color: #fff;
      border: none;
    }
  }
}

.about-designer__about_menu{
  width: 100%;
  float: left;
  display: inline-block;
  padding: 0;
  margin-bottom: 60px;
  li{
    list-style-type: none;
    display: inline-block;
    margin: 0 18px;
    &:first-of-type{
      margin-left: 0;
    }
    a{
      font-size: 17px;
      line-height: 32px;
      color: #000;
      font-family: $fnm;
      font-weight: 500;
    }
  }
}

.about-designer__pic{
  width: 50%;
  float: left;
  position: relative;
  // height: 595px;
  overflow: hidden;
  img{
    width: auto;
    height: 594px;
    object-fit: contain;
    margin-top: 7px;
    position: relative;
  }
}
