@mixin  tin_tak{
	color: #000;
	font-family: $fn;
	font-size: 17px;
	font-weight: 300;
	line-height: 32px;
	min-width: 180px;
	background-color: #f7f7f7;
	border: 1px solid #979797;
	text-align: center;
	width: 231px;
	&:hover{
		color: #fff;
		transition: 0.3s;
	  background-color: #000;
	}
}
@mixin h2-title {
	width: 100%;
	font-family: $fnm;
	font-size: 50px;
	font-weight: 500;
	color: #000;
	text-align: center;
	display: inline-block;
	margin-top: 52px;
}
@mixin span-title {
	color: #959595;
	font-family: $fnl;
	font-size: 20px;
	font-weight: 300;
	line-height: 28px;
	text-align: center;
	float: left;
	width: 100%;
	display: inline-block;
	padding: 0 36%;
	box-sizing: border-box;
}
@mixin transition($t) {
	-webkit-transition: all $t linear;
	   -moz-transition: all $t linear;
	    -ms-transition: all $t linear;
	     -o-transition: all $t linear;
	        transition: all $t linear;
}

@mixin overflow-img() {
	overflow: hidden;
	img {
		min-width: 100%;
		height: auto;
		position: relative;
		left: 50%;
		top: 50%;
		-webkit-transform: translate(-50%,-50%);
		   -moz-transform: translate(-50%,-50%);
		    -ms-transform: translate(-50%,-50%);
		     -o-transform: translate(-50%,-50%);
		        transform: translate(-50%,-50%);
	}
}

@mixin border-box{
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	-ms-box-sizing: border-box;
}


@mixin vertical-align {
	position: relative;
	top: 50%;
	-webkit-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
}

// Breakpoints for each query
$smartphone: 480px;
$tabletPortrait: 767px;
$tabletLandscape: 1024px;
$desktop: 1174px;
$largeScreen: 1400px;

@mixin respondTo($media) {
	@if $media == smartphone {
	  @media (min-width: $smartphone) {
			@content;
		}
	}
	@else if $media == tablet {
	  @media (min-width: $tabletPortrait) and (max-width: $tabletLandscape) {
			@content;
		}
	}
	@else if $media == smallScreen {
	  @media (min-width: $desktop) {
			@content;
		}
	}
	@else if $media == desktop {
	  @media (min-width: $desktop) {
			@content;
		}
	}
  @else if $media == largeScreen {
    @media (min-width: $largeScreen) {
			@content;
		}
  }
}
