.tools{
  width: 100%;
  float: left;
  position: relative;
  text-align: center;
  h2{
    @include h2-title;
    margin-top: 74px;
  }
  span{
    @include span-title;
    margin-bottom: 46px;
  }
  .title__tabs{
   width: 100%;
   float: left;
   position: relative;
   box-sizing: border-box;
   margin-bottom: 10px;
   overflow: hidden;
   ul{
     padding: 0;
     background-color: #fff;
     width: 100%;
     display: block;
     margin: 0 auto;
     width: 2700px;
     li, .slick-slide{
       width: 352px!important;
       float: left;
       display: block;
       list-style-type: none;
       text-align: center;
      position: relative;
      text-align: center;
      background-color: rgba(160, 160, 160, 0.1);
      outline: none;
      cursor: pointer;
      margin: 0 10px 0 0;
      height: 200px;
      padding: 51px 39px;
      box-sizing: border-box;
      outline: none;

      &:hover{
        color: #fff;
        transition: 0.3s;
        background-color: #000;
        h2, span{
          color: #fff;
        }
      }
       h2{
         color: #000;
         font-family: $fnm;
         font-size: 32px;
         font-weight: 500;
         width: 100%;
         text-align: center;
         margin-top: 0;
         margin-bottom: 10px;
         display: inline-block;
       }
       span{
         color: #959595;
         font-family: $fnb;
         font-size: 20px;
         font-weight: 300;
         line-height: 28px;
         width: 100%;
         text-align: center;
         padding: 0;
       }
     }
   }
 }
 .page__tabs_target_tools{
   width: 100%;
   float: left;
   display: inline-block;
   background-color: #fff;
   text-align: center;
 }
 li.page__tabs_target_tools.page__tabs_active{
   display: block;
   background-color: #000;
     h2, span{
       color: #fff;
     }

     &:hover{
       transition: 0.3s;
     }
 }
 .box-connent{
   width: 100%;
   float: left;
   position: relative;
   box-sizing: border-box;
 }

 .page__tabcontent_tools{
   width: 100%;
   float: left;
   box-sizing: border-box;
   .item{
     display: inline-block;
     width: 100%;
     padding: 10px 325px 20px 240px;
     box-sizing: border-box;
     height: 672px;
     img{
       size: 100%;
     }
   }
   .item__about{
     width: calc(100% - 400px);
     float: left;
     position: relative;
     padding-top: 104px;
     box-sizing: border-box;
     h3{
       font-family: $fnm;
       font-size: 32px;
       font-weight: 500;
       color: #000;
       width: 100%;
       text-align: left;
       display: inline-block;
       margin-bottom: 18px;
     }
     p{
       width: 100%;
       float: left;
       position: relative;
       display: inline-block;
       color: #0d0d0d;
       font-family: $pdr;
       font-size: 17px;
       line-height: 32.44px;
       margin-bottom: 38px;
       text-align: left;
       &:first-of-type{
         color: #959595;
         font-family: $pdr;
         font-size: 20px;
         font-weight: 400;
         line-height: 34.64px;
         margin-bottom: 22px;
         width: 82%;
         text-align: left;
       }
     }
   }
   .item__thumb{
     size: 400px 590px;
     display: inline-block;
     float: left;
     overflow: hidden;
     padding-left: 3px;
     box-sizing: border-box;
     img{
       size: 100%;
       object-fit: contain;
       margin-top: 33px;
     }
   }
 }

 .one2{
   display: block;
 }
 .two2, .three2, .four2, .five2, .six2{
   display: none;
 }
}
