.contacts{
  width: 100%;
  float: left;
  position: relative;
  text-align: center;
  padding: 65px 0;
  box-sizing: border-box;
  border-bottom: 1px solid #f5f5f5;
  h2{
    @include h2-title;
    margin-top: 39px;
    margin-bottom: 27px;
  }
  span{
    @include span-title;
    padding: 0 33%;
    letter-spacing: 0.2px;
  }
}
.contacts__row{
  display: inline-block;
  float: none;
  margin: 29px auto;
width: 719px;
  p{
    text-align: left;
    width: 100%;
    float: left;
    color: #000;
    font-family: $fnm;
    font-size: 24px;
    font-weight: 500;
    // line-height: 17px;
    margin: 19px 0 13px;
    overflow: hidden;
    letter-spacing: 0.4px;
    span{
      width: 118px;
      display: inline-block;
      text-align: left;
      color: #959595;
      font-family: $pdr;
      font-size: 20px;
      font-weight: 400;
      line-height: 17px;
      padding: 0;
      margin-top: 10px;
    }
    a{
      color: #000;
      font-family: $fnb;
      font-size: 24px;
      font-weight: 300;
      // line-height: 17px;
      display: inline-block;
      border-bottom: 2px solid transparent;
      position: relative;
      margin-left: 6px;
      letter-spacing: 0.2px;
      &:after{
        position: absolute;
        content: "";
        display: inline-block;
        background-color:#f33;
        width: 100%;
        left: 0;
        margin-top: 33px;
        height: 2px;
      }
      &:hover{

        transition: 0.3s;
      }
    }

  }
  p:last-of-type{
    padding-bottom: 15px;
    box-sizing: border-box;
  }
  .icon{
    height: 21px;
    width: initail;
    position: relative;
    float: left;
    margin-left: 0;
    object-fit: contain;
    line-height: 25px;
    top: 9px;
    margin-right: 17px;
    &:hover{
      border-bottom: none;
      transition: 0.3s;
    }
  }
}
