.power{
  width: 100%;
  float: left;
  position: relative;
  text-align: center;
  h2{
    @include h2-title;
    margin-bottom: 12px;
  }
  span{
    @include span-title;
    margin-bottom: 50px;
  }
}

.power__items{
  background-color: #f5f5f5;
  width: 49.6%;
  float: left;
  height: 512px;
  padding: 39px 81px 42px 118px;
  box-sizing: border-box;
  margin-top: 11px;
  margin-right: 11px;
  &:nth-of-type(2), &:nth-of-type(4){
    margin-right: 0;
  }
  .thumb{
    width: 111px;
    height: 76px;
    float: left;
    display: inline-block;
    overflow: hidden;
    svg{
      object-fit: contain;
      float: left;
    }
  }
  h3{
    font-family: $fnm;
    font-size: 32px;
    font-weight: 500;
    color: #000;
    width: 100%;
    text-align: left;
    display: inline-block;
    margin-bottom: 18px;
    margin-top: 17px;
  }
  p{
    width: 100%;
    float: left;
    position: relative;
    display: inline-block;
    color: #0d0d0d;
    font-family: $pdr;
    font-size: 17px;
    line-height: 32.44px;
    margin-bottom: 38px;
    text-align: left;
    &:first-of-type{
      color: #959595;
      font-family: $pdr;
      font-size: 20px;
      font-weight: 400;
      line-height: 34.64px;
      margin-bottom: 22px;
      width: 100%;
      text-align: left;
    }
  }
  ul{
    padding: 0;
    display: inline-block;
    width: 100%;
    float: left;
    li{
      width: 50%;
      float: left;
      display: inline-block;
      list-style-type: none;
      &::before{
        content: "-";
        float: left;
      }
      color: #0d0d0d;
      font-family: $pdr;
      font-size: 17px;
      font-weight: 400;
      line-height: 32px;
      text-align: left;
    }
  }
}
